import React, { Component, PropTypes } from 'react';
import { TextBox, DropDown } from 'react-form-elements';


export default class Picker extends Component {
    constructor(props) {
        super(props); 
    }
    get to() {
        return this.refs.to.getValue();
    }
    get from() {
        return this.refs.from.getValue();
    }
    isDisabled(val, con) {
        return val === con();
    }
    render() {

        return (
            <div className="picker">
                <DropDown ref="from" label="From Currency">
                    <option value="NZD" disabled={this.isDisabled('NZD', this.to)}>New Zealand</option>
                    <option value="USD" disabled={this.isDisabled('USD', this.to)}>America</option>
                    <option value="AUD" disabled={this.isDisabled('AUD', this.to)}>Australia</option>
                    <option value="GBP" disabled={this.isDisabled('GBP', this.to)}>UK Pound</option>
                    <option value="EUR" disabled={this.isDisabled('EUR', this.to)}>Euro</option>
                </DropDown>
                <DropDown ref="to" label="To Currency">
                    <option value="USD">America</option>
                    <option value="NZD">New Zealand</option>
                    <option value="AUD">Australia</option>
                    <option value="GBP">UK Pound</option>
                    <option value="EUR">Euro</option>
                </DropDown>
            </div>
            );
    }
}

Picker.propTypes = { to: PropTypes.string, from: PropTypes.string };
Picker.defaultProps = { to: 'NZD', from: 'USD' };


